import { styled } from '@mui/material';

export const Divider = styled('div')`
  position: relative;

  &:before {
    content: '';
    position: absolute;
    left: 35%;
    right: 35%;
    height: 2px;
    background-image: ${({ theme }) =>
      `linear-gradient(to right, transparent, ${theme.palette.primary.main}, transparent)`};
  }
`;
